import * as React from 'react';
import { FormattedMessage } from 'react-intl';
import * as dayjs from 'dayjs';
import { eos_instance } from '../utils/eos';
import { banker_account } from '../utils/config';
import { reverse } from 'lodash';
import dice1 from 'src/assets/table_dices/bets_dice_1@3x.png';
import dice2 from 'src/assets/table_dices/bets_dice_2@3x.png';
import dice3 from 'src/assets/table_dices/bets_dice_3@3x.png';
import dice4 from 'src/assets/table_dices/bets_dice_4@3x.png';
import dice5 from 'src/assets/table_dices/bets_dice_5@3x.png';
import dice6 from 'src/assets/table_dices/bets_dice_6@3x.png';

const dices = [
  dice1,
  dice2,
  dice3,
  dice4,
  dice5,
  dice6,
];

interface TableProps {}

interface TableState {
  data:TableItems[];
}

export interface ApiRes {
  player:string;
  payin:string;
  payout:string;
  dice:{ // 开奖结果
    dice1:number;
    dice2:number;
    dice3:number;
  };
  payed:1|0;
  uid:string;
  detail:string; // 下注信息
  
}

interface TableItems {
  uid:string;
  time:dayjs.Dayjs;
  player:string;
  result:number[];
  betting:string;
  win:string;
}

export class Table extends React.Component<TableProps, TableState> {
  protected rows_count = 20;
  public constructor(props:TableProps) {
    super(props);
    this.state = {
      data:[],
    };
    this.get_new_actions = this.get_new_actions.bind(this);
  }
  
  public componentDidMount() {
    this.get_new_actions();
  }
  
  public get_new_actions() {
    // 操作记录
    eos_instance.getActions(banker_account, -1 , -90).then(({actions}:any) => {
      const result:TableItems[] = [];
      actions.forEach((action:any) => {
        if (
          result.length < this.rows_count
          && action.action_trace
          && action.action_trace.act
          && action.action_trace.act.account === banker_account
          && action.action_trace.act.name === 'result'
          && action.action_trace.act.data
          && action.action_trace.act.data.res
          && action.action_trace.receipt
          && action.action_trace.receipt.receiver === banker_account) {
          const res:ApiRes = action.action_trace.act.data.res;
          const data:TableItems = {
            time:dayjs(action.block_time),
            player:res.player,
            result:[
              res.dice.dice1,
              res.dice.dice2,
              res.dice.dice3,
            ],
            betting:res.payin,
            win:res.payout,
            uid:res.uid,
          };
          result.push(data);
        }
      })
      this.setState({
        data:reverse(result),
      })
    }).catch(() => {
      // alert('error');
    });
  }
  
  public render(){
    const list = this.state.data.map((item) => {
      return this.render_row(item);
    })
    return (
      <div style={styles.wrapper}>
        <div style={styles.title}>
          <FormattedMessage
            id='all_bets'
          />
          <FormattedMessage
            id='my_bets'
          />
        </div>
        <table className={'best-table'} cellSpacing={0}>
          <thead>
          <tr>
            <th>
              <FormattedMessage
                id='time'
              />
            </th>
            <th>
              <FormattedMessage
                id='player'
              />
            </th>
            <th>
              <FormattedMessage
                id='lottery'
              />
            </th>
            <th>
              <FormattedMessage
                id='betting'
              />
            </th>
            <th>
              <FormattedMessage
                id='win'
              />
            </th>
          </tr>
          </thead>
          <tbody>
          {list}
          </tbody>
        </table>
      </div>
    );
  }
  
  private render_row = (item:TableItems) => {
    return (
      <tr key={item.uid}>
        <td>{item.time.format('HH:mm:ss')}</td>
        <td>{item.player}</td>
        <td>{this.render_bets(item.result)}{this.render_result_tips(item.result)}</td>
        <td>{item.betting}</td>
        <td>{item.win}</td>
      </tr>
    );
  }
  
  private render_result_tips(result:number[]) {
    const total = result[0] + result[1] + result[2];
    let tips = total > 9 ? "BIG" : "SMALL";
    if (result[0] === result[1] && result[1] === result[2]) {
      tips = 'TRIPLE'
    }
    tips += ' ' + total;
    return tips;
  }
  
  private render_bets(result:number[]) {
    const lists = result.map((number) => {
      return (
        <img style={styles.dice} src={dices[number - 1]}/>
      );
    });
    return (
      <span>
        {lists}
      </span>
    );
  }
}

const styles:{[key:string]:React.CSSProperties} = {
  dice:{
    width:11,
    height:12,
  },
  wrapper:{
    paddingTop:40,
    paddingBottom:20,
    background:'#391514'
    },
  title:{
    display:'flex',
    justifyContent:'space-between',
    paddingBottom:10,
    paddingLeft:50,
    paddingRight:50,
    color:'white',
    fontWeight:'bolder'
  },
  table:{},
};


