import * as React from 'react';
import { slide as Menu } from 'react-burger-menu';
import { FormattedMessage } from 'react-intl';
import { langType } from '../App';
import header_mask from 'src/assets/header_mask.png';
import china_icon from 'src/assets/country/china.png';
import us_icon from 'src/assets/country/us.png';
import pop_icon from 'src/assets/pop.png';
import menu_icon from 'src/assets/menu.png';
import chinese from 'src/assets/country/chinese.png';
import chinese_selected from 'src/assets/country/chinese_selected.png';
import english from 'src/assets/country/english.png';
import english_selected from 'src/assets/country/english_selected.png';
import logo from 'src/assets/logo.png';

interface HeaderProps {
  on_change_lang:(lang:langType) => void;
  scatter:any;
  onLogin:() => void;
  selected_land:langType;
}

interface HeaderState {
  player_name?:string;
  show_options:boolean;
}

interface LandTypeOption {
  lang:langType;
  icon:any;
  option_icon:any;
  selected_option_icon:any;
}

const langTypeOptions:LandTypeOption[] = [
  {
    icon:china_icon,
    lang:'zh-CN',
    option_icon:chinese,
    selected_option_icon:chinese_selected,
  },
  {
    icon:us_icon,
    lang:'en-US',
    option_icon:english,
    selected_option_icon:english_selected,
  }
];

export class Header extends React.Component<HeaderProps, HeaderState > {
  
  public constructor(props:HeaderProps) {
    super(props);
    this.on_change_lang = this.on_change_lang.bind(this);
    this.render_select_lang_btn = this.render_select_lang_btn.bind(this);
    this.change_options_visible = this.change_options_visible.bind(this);
    this.state = {
      show_options:false,
    };
  }
  
  public render(){
    return (<div style={styles.wrapper}>
      { this.render_logo() }
      { this.render_login_btn() }
      { this.render_select_lang_btn() }
      {/*{ this.render_menu_btn() }*/}
      { this.state.show_options && this.render_options() }
    </div>);
  }
  
  private render_logo() {
    return (
      <span style={styles.logo}>
        <img style={{width:100, height:30}} src={logo} alt=""/>
      </span>
    );
  }
  
  private render_login_btn() {
    if (this.props.scatter && this.props.scatter.identity) {
      const account_name = this.props.scatter.identity.accounts.find((account:any) => account.blockchain === 'eos').name;
      return (<span style={styles.login}>{account_name}</span>)
    }
    return (
      <span style={styles.login} onClick={this.props.onLogin}>
        <FormattedMessage id='login'/>
      </span>
    );
  }
  
  private render_menu_btn() {
    return (
      <div>
        <img style={styles.menu_btn} src={menu_icon}/>
        <Menu right={true} >
          <div style={{width:200, backgroundColor:'red'}}>
            <div>规则说明</div>
            <div>分享</div>
          </div>
        </Menu>
      </div>
    );
  }
  
  private render_select_lang_btn() {
    const { selected_land } = this.props;
    const icon = langTypeOptions.find((option) => {
      return option.lang === selected_land;
    });
    return (
      <div onClick={this.change_options_visible} style={styles.select_lang_btn}>
        <img style={{width:19, height:13, marginRight:8}} src={(icon as LandTypeOption).icon} />
        <img style={{width:10, height:6}} src={pop_icon} />
      </div>
    );
  }
  
  private change_options_visible() {
    const { show_options } = this.state;
    this.setState({show_options:!show_options});
  }
  
  private render_options() {
    const options = langTypeOptions.map((item) => {
      return this.render_option(item);
    });
    return (
      <div style={styles.options_wrapper}>
        {options}
      </div>
    );
  }
  
  private render_option(option:LandTypeOption) {
    const { selected_land } = this.props;
    const is_selected = selected_land === option.lang;
    return (
      <span onClick={this.select_option.bind(this, option.lang)} style={{width:78, height:23}} key={option.lang}>
        <img
          style={is_selected ? styles.selected_option : styles.option}
          src={is_selected ? option.selected_option_icon : option.option_icon}/>
      </span>
    );
  }
  
  private select_option(lang:langType) {
    this.props.on_change_lang(lang);
  }
  
  private on_change_lang(event:any) {
    this.props.on_change_lang(event.target.value);
  }
}

const styles:{[key:string]:React.CSSProperties} = {
  logo:{
    position:'absolute',
    left:10,
    top:10,
    color:'#9B9B9C',
  },
  login:{
    position:'absolute',
    right:105,
    color:'#9B9B9C',
  },
  select_lang_btn:{
    display:'flex',
    alignItems:'center',
    position:'absolute',
    right:50,
  },
  menu_btn:{
    width:18,
    height:13,
    position:'absolute',
    right:22,
    top:17,
  },
  options_wrapper:{
    position:'absolute',
    display:'flex',
    flexDirection:'column',
    top:47,
    right:25,
    zIndex:2,
  },
  option:{
    width:55,
    height:13,
    paddingLeft:9,
    paddingTop:4,
    paddingRight:15,
    paddingBottom:7,
    backgroundColor:'#FFFFFF',
  },
  selected_option:{
    width:78,
    height:23,
  },
  wrapper:{
    display:'flex',
    backgroundImage:`url(${header_mask})`,
    height:47,
    justifyItems:'center',
    alignItems:'center',
  }
};


