import * as React from 'react';
import winIcon from 'src/assets/result/+@3x.png';
import loseIcon from 'src/assets/result/-@3x.png';
import mask from 'src/assets/result/mask@3x.png';
import pop_lose from 'src/assets/result/pop_lose@3x.png';
import pop_win from 'src/assets/result/pop_win@3x.png';

interface ResultProps {
  win:boolean;
  result:string;
}

interface ResultState {
}


export class Result extends React.Component<ResultProps, ResultState > {
  
  public constructor(props:ResultProps) {
    super(props);
    this.state = {
    };
  }
  
  public render(){
    const { win, result } = this.props;
    return (<div style={styles.wrapper}>
      <img style={{width:328, height:192, position:'absolute',top:100}} src={win ? pop_win : pop_lose} />
      <div style={{color:'rgba(254,254,254,1)', fontSize:20, position:'absolute',top:210}}>
        KIKYOU
      </div>
      <div style={{position:'absolute', color:'rgba(254,254,254,1)', fontSize:25,top:240}}>
        <img style={{width:21, height:14, marginRight:10}} src={win ? winIcon : loseIcon}/>
        {result}
      </div>
    </div>);
  }
  
}

const styles:{[key:string]:React.CSSProperties} = {
  wrapper:{
    flexDirection:'column',
    display:'flex',
    position:'fixed',
    backgroundImage:`url(${mask})`,
    backgroundSize:'cover',
    width:'100%',
    height:'100%',
    justifyContent:'center',
    alignItems:'center',
  }
};


